.home {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.img1 {
    margin-right: 10px;
}

.img1, .img2 {
    height: 1000px;
    width: 47vw;
    min-width: 300px;
    object-fit: cover;
}

@media only screen and (max-width: 610px) {
    .img1, .img2 {
        height: 400px;
        width: 94.5vw;
        margin: 0;
    }

    .img {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }
}

.img3 {
    height: 730px;
    width: 94.5vw;
    object-fit: cover;
    margin-top: 30px;
}

.advantages {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 80vw;
    margin-top: 50px;
    margin-bottom: 50px;
}

.advantages>div>img {
    height: 50px;
    width: 50px;
    object-fit: cover;
    margin-bottom: 30px;
}

.advantages>div {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    height: 125px;
    width: 330px;
    text-align: center;
}

.advantages>div>p {
    font-family: 'Nunito', sans-serif;
}

.home>a {
    margin-bottom: 40px;
    text-decoration: none;
    font-family: 'Nunito', sans-serif;
}

.instagram-photos>a>img {
    height: 22vw;
    width: 22vw;
    margin: 5px;
    object-fit: cover;
}

.home>p {
    font-family: 'Nunito', sans-serif;
    margin-top: 50px;
    margin-bottom: 50px;
}

.input-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 50px;
}

.email-input {
    display: inline-block;
    width: 350px;
    height: 45px;
    padding: 15px;
    border: none;
    outline: none;
    position: relative;
    margin-bottom: 50px;
}

.sub-button, .addProduct>button {
    width: 150px;
    height: 45px;
    background-color: rgb(32, 37, 75);
    border: none;
    outline: none;
    color: white;
    position: relative;
}

/* ----------------------------------------------------------- */

.contact-form {
    width: 70vw;
    max-width: 500px;
    height: 400px;
    margin: auto;
    margin-top: 60px;
}

.contact-form>input, .inputImgLink>input, .productDetails>input {
    border-top: none;
    border-left: none;
    border-right: none;
}

.contact-form>*, .register-form>* {
    margin-bottom: 20px;
}

.contact-form>h4, .register-form>h4 {
    color: red;
}

.register-form {
    width: 70vw;
    max-width: 500px;
    height: 450px;
    margin: auto;
    margin-top: 60px;
}

/* ---------------------------------------------------------- */

.product_container {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

/* ---------------------------------------------------------- */

.productPage>div:nth-child(1) {
    width: 50vw;
}

.primaryImage {
    width: 48.6vw;
    height: 50vw;
    object-fit: cover;
}

.additionalImages {
    width: 100%;
    height: 62vw;
    object-fit: cover;
    margin-top: 0.6vw;
    margin-right: 0.6vw;
}

#saveChanges {
    display: none;
}

.productDescription{
    display: flex;
    flex-direction: column;
    padding: 5vw;
    width: 100%;
    height: 50vw;
    justify-content: space-around;
}

.productDescription > textarea {
    height: 200px;
}

.productDescription > * {
    border: none;
    outline: none;
}

@media only screen and (min-width: 1000px) {
    .additionalImages {
        width: 48%;
        height: 42vw;
    }
    .primaryImage {
        margin-right: 1.5vw !important;
    }
}

.productPage {
    display: flex;
    width: 85vw;
    margin: auto;
}

@media only screen and (max-width: 800px) {
    .productPage {
        display: flex;
        flex-direction: column;
    }

    .productPage>div:nth-child(1) {
        display: flex;
        gap: 5px;
        height: 100vw;
        width: 88vw !important;
        overflow-x: visible;
        overflow-y: hidden !important;
    }

    .productPage>div:nth-child(1)>img {
        margin: 0;
        width: 90vw !important;
        height: 110vw;
    }

    .addProduct>div {
        flex-direction: column-reverse;
        height: 1000px;
        align-items: center;
        justify-content: space-between;
    }

    .productDetails {
        height: 350px;
        min-width: 250px;
    }

    .inputImgLink {
        height: 400px;
        width: 40vw !important;
    }
}

/* ---------------------------------------------------------- */

.addProduct>div {
    display: flex;
    min-height: 400px;
    justify-content: space-around;
}

.inputImgLink {
    display: flex;
    flex-direction: column;
    width: 30vw;
    min-width: 250px;
    justify-content: space-between;
}

.productDetails {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 40vw;
}

.addProduct>button {
    margin: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 50px 0 20px 0;
}

/*--------------------------------------------------------------*/

.logout_div {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    height: 400px;
}

.logout_button {
    margin-top: 100px;
    border: none;
    width: 150px;
    height: 50px;
    font-size: 20px;
    color: red;
}